<script>
  import PagesGrid from "./Pages/PagesGrid.svelte";
  import Header from "./UI/Header.svelte";
  import TextInput from "./UI/TextInput.svelte";

  let title = "";
  let subtitle = "";
  let description = "";
  let image = "";

  let pages = [
    {
      id: 1,
      title: "Page 1",
      subtitle: "subtitle",
      description: "description",
      image: "https://i.imgur.com/gt74EGw.jpg",
    },
    {
      id: 2,
      title: "Page 2",
      subtitle: "subtitle 2",
      description: "description",
      image: "https://i.imgur.com/mgfgDYb.jpg",
    },
  ];

  const addPage = ({ target }) => {
    const newPage = {
      id: Math.random(),
      title: target.title.value,
      subtitle: target.subtitle.value,
      description: target.description.value,
      image: target.image.value,
    };
    pages = [newPage, ...pages];
  };
</script>

<style>
  input,
  textarea {
    display: block;
    width: 100%;
    font: inherit;
    border: none;
    border-bottom: 2px solid #ccc;
    border-radius: 3px 3px 0 0;
    background: white;
    padding: 0.15rem 0.25rem;
    transition: border-color 0.1s ease-out;
  }

  input:focus,
  textarea:focus {
    border-color: #e40763;
    outline: none;
  }

  label {
    display: block;
    margin-bottom: 0.5rem;
    width: 100%;
  }

  .form-control {
    padding: 0.5rem 0;
    width: 100%;
    margin: 0.25rem 0;
  }
</style>

<Header />
<main>
  <h1>Pages</h1>

  <form on:submit|preventDefault={addPage}>
    <TextInput controlType="input" value="title" label="Title" />
    <div class="form-control">
      <label for="subtitle">Subtitle</label>
      <input type="text" id="subtitle" bind:value={subtitle} />
    </div>
    <div class="form-control">
      <label for="description">Description</label>
      <textarea rows="3" id="description" bind:value={description} />
    </div>
    <div class="form-control">
      <label for="image">Image</label>
      <input type="text" id="image" bind:value={image} />
    </div>
    <button type="submit">Submit</button>
  </form>
  <PagesGrid {pages} />
</main>
